<template>
  <div id="home">
    <div v-if="err">
      Error in loading data.
    </div>
    <div v-else>
    <div v-for="coin in cryptos">
      <div class="container card" :key="coin.id">
      <header class="card-header">
        <p class="card-header-title">{{coin.name}} | {{coin.symbol}} </p>
      </header>
      <div class="card-content">
        <div class="content">
          <span>&#8377;</span> {{coin.price}}
        </div>
      </div>
    </div>
    <br>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return{
      title:'Click To Load Real Time Bitcoin Data',
      cryptos:[],
      err:''
    }
  },
  created(){
    this.fetchBit()
  },
  methods:{
    fetchBit:function(){
      fetch('https://api.coinmarketcap.com/v1/ticker/?convert=INR&limit=10').then(res => res.json())
      .then(data=>data.forEach(crytpo=>{
        const tempdata = {
          id: crytpo.id,
          name:crytpo.name,
          symbol:crytpo.symbol,
          price:crytpo.price_inr
        }
        this.cryptos.push(tempdata)
      })).catch(error =>{
        this.err = true;
        console.log(error);
      })
    }
  }
  
}
</script>


